// Wrapper

.product-card__wrapper {
  display: flex;
}

// Text block

.product-card__text-block {
  order: 2;
  display: flex;
  flex-direction: column;
}

// Subtitle

.product-card__subtitle {
  margin-bottom: 10px;
}

// Text

.product-card__text {
  margin-top: auto;
}

// Image block

.product-card__image-block {
  margin-right: 20px;
  order: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}

// Image

.product-card__img {
  width: 200px;
  height: auto;
}
