// Subtitle

.question-form__subtitle--add {
  margin-bottom: 35px;

  @media (min-width: $tablet-width) {
    margin-bottom: 57px;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 57px;
  }
}

// Container

.add-info__container {
  display: flex;
  flex-direction: column;

  @media (min-width: $tablet-width) {
    margin-top: -16px;
    margin-right: -29px;
    margin-bottom: -17px;
    margin-left: -29px;
    flex-direction: row;
    flex-wrap: wrap;
  }

  @media (min-width: $desktop-width) {
    margin-right: -52px;
    margin-bottom: -19px;
    margin-left: -52px;
  }
}

// Item

.add-info__item {
  margin-bottom: 14px;

  @media (min-width: $tablet-width) {
    margin-top: 16px;
    margin-right: 29px;
    margin-bottom: 17px;
    margin-left: 29px;
    flex: 0 0 calc(-58px + 50%);
  }

  @media (min-width: $desktop-width) {
    margin-right: 52px;
    margin-left: 52px;
    flex: 0 0 221px;
  }
}

// Property

.add-info__property {
  --box-size: 30px;
  --check-icon-width: 18px;
  --check-icon-height: 14px;
  padding-top: 5px;
  padding-bottom: 5px;
  padding-left: 46px;
  box-sizing: border-box;
  position: relative;
  display: flex;
  align-items: center;
  cursor: pointer;

  &::before,
  &::after {
    content: "";
    box-sizing: border-box;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    transition: all 0.3s ease-out;
  }

  &::before {
    display: none;
    width: var(--check-icon-width);
    height: var(--check-icon-height);
    left: calc((var(--box-size) - var(--check-icon-width)) / 2);
    background-image: url("data:image/svg+xml, %3Csvg viewBox='0 0 19 14' xmlns='http://www.w3.org/2000/svg' stroke-width='2' stroke='rgb(104, 183, 56)'%3E%3Cpath d='M1 6 L 7 13 L 18 1' fill='none' /%3E%3C/svg%3E");
    background-repeat: no-repeat;
  }

  &::after {
    width: var(--box-size);
    height: var(--box-size);
    left: 0;
    border: 1px solid var(--cl-brd-form-box);
  }

  @media (min-width: $tablet-width) {
    --box-size: 40px;
    padding-left: 59px;
    flex: 1 1 auto;
  }
}

// Checkbox active

.add-info__checkbox:hover + .add-info__property::after,
.add-info__checkbox:checked:hover + .add-info__property::after,
.add-info__checkbox:focus + .add-info__property::after {
  border-color: var(--cl-brd-form-box-hover);
}

.add-info__checkbox:disabled + .add-info__property {
  opacity: 0.3;
}

.add-info__checkbox:checked + .add-info__property::before {
  display: block;
}
